.CloseButton {
    float: right;
    cursor: pointer;
    margin-left: 10px;
    color: black;
}

.CloseButton:hover {
    color: red;
}

.tabcontainer {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;    
    position: absolute;
    height: 100%;
    width: 100%;
}

.tabbar {
    position: relative;
    display: -ms-flexbox;
    display: flex;
    background: #012;
    padding-top: 5px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    min-height: 33px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;    
}

.tabitem {
    background: #234;
    position: relative;
    padding: 3px 15px 3px 5px;
    -ms-flex-positive: 0;
        flex-grow: 0;
    border-radius: 5px 20px 0px 0px;
    border: solid 1px #345;
    border-bottom: 0px;
    margin-right: -1px;
    cursor: pointer;
    color: #567;
    overflow: hidden;
}

.tabitem[active=true] {
    background: #345;
    color: #def;
    border: solid 1px #345;
}



